:root {
  --krc-primary-color: #ffab39;
  --krc-secondary-color: #fa5742;
  --krc-grey-color: #999999;
}

.Collapsible {
  padding: 8px 15px;
  /*border: 1px solid #ededed;*/
  border-top: 0px;
}

.Collapsible__contentInner {
  padding-top: 16px;
}

.Collapsible__trigger {
  display: block;
  position: relative;
  padding-right: 30px;
}

.Collapsible:focus-within {
  outline-offset: -2px;
  outline: 5px #639cf2 auto;
}

.Collapsible__trigger:after {
  font-family: 'FontAwesome';
  content: '\f107';
  position: absolute;
  right: 10px;
  top: 0px;
  display: block;
  transition: transform 300ms;
}

.Collapsible__trigger.is-open:after {
  transform: rotateZ(180deg);
}

.sharedaddy {
  margin-top: 32px;
}

.exclamation-triangle:after {
  content: '\f071';
  font-family: 'FontAwesome';
}

.even {
  background-color: #ccc;
}

